import MescrollUni  from 'mescroll-uni'
import AutoMenu from '@/components/common/AutoMenu.vue'
import Panel from '@/components/common/Panel.vue'
import ArticleItem from './components/ArticleItem.vue'
export default {
	components: {
		AutoMenu: AutoMenu,
		MescrollUni: MescrollUni,
		Panel: Panel,
		ArticleItem: ArticleItem
	},
	data() {
		return {
			menus: [],
			flexDatas: [],
			downOptions: {
				auto:true
			},
			upOptions: {
				auto: false
			}
		}
	},
	onLoad() {
		// 获取菜单
		this.getMenus()
	},
	computed:{
		cateMenus(){
			return this.menus.map(cate => cate.name)
		}
	},
	methods: {
		// 下拉刷新
		downCallback(mescroll){
			this.$uRequest.get({
				url: '/mini/home/index',
				success: (res) => {
					if(res.code === 0){
						this.flexDatas.splice(0,this.flexDatas.length,...res.data)
						mescroll.endSuccess(this.flexDatas.length,true)
					}else{
						mescroll.endErr()
					}
				},
				fail: (err) => {
					console.log(JSON.stringify(err))
					mescroll.endErr()
				}
			})
		},
		// 上拉刷新
		upCallback(mescroll){
		},
		// 获取大菜单
		getMenus(){
			this.$uRequest.get({
				url: '/mini/home/mainCates',
				success: (res) => {
					if(res.code === 0){
						this.menus.splice(0,this.menus.length,...res.data.items)
					}else{
						
					}
				},
				fail: (err) => {
					console.log(JSON.stringify(err))
				}
			})
		}
	}
}
